:root{
    --primary: #fff;
}

.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor:pointer;
}

.btn--primary{
    background-color: var(--primary);
    color: #242424;
    border: solid 1px var(--primary);
}

.btn--outline{
    background-color: transparent;
    color:#fff;
    padding: 8px 20px;
    border: solid 1px var(--primary);
    transform: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background-color: rgb(255, 94, 0);
    color: #242424;
    transform: all 0.3s ease-out;
}